
@keyframes colorChange {
  from  {opacity: 0;
         display: none;
  }
  to {opacity: 1;
         display: block;
  }
}
@keyframes commentAnimation {
  from {
    opacity: 0;
    margin-right: 90%;
    
  }
  to {
    opacity: 1;
    margin: 10px 1%;
    
  }
}

@keyframes postAnimation {
  from {
    opacity: 0;
    margin: 0px 30px 30px 30px;
  }
  to {
    opacity: 1;
    margin: 30px 30px 30px 30px;
  }
}
.App {
  text-align: center;
}
* {
  box-sizing: border-box;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.search-button {
  border: 2px solid #847A7A;
  border-radius: 100px;
  font-family:  monospace;
  font-size: 18px;
  margin-left: 25px;
}

.searchDiv {
  display: block;
  margin: 0 25%;
  width: 80%;
  height: 150px;
  
}
#searchSpace {
  width: 50%;
  height: 70px;
  font-size: 30px;
  text-align: center;
  font-family: Helvetica;
  margin-left: 5%;
  margin: 5px;
}
.logoImage {
  display: block;
  margin-bottom: 40px;
  margin-top: 30px;

  width: auto;
}
.logoImage img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 8%;
  height: auto;
}
.mainTitle {
  display: flex;
  justify-content: center;
  height: 50px;
  width: 300px;
  text-align: center;
  margin: auto;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  font-size: 2em;
  color: rgb(39, 49, 42);
  background-color: hsl(20, 100%, 60%);
  
}
.post {
  display: block;
  opacity: 0;
  animation-name: postAnimation;
  animation-duration: 1s ;
  animation-fill-mode: both;
  padding: 0px 30px 0px 50px;
  margin: 30px 30px 30px 30px;
  border: 2px solid #000000;
  background-color: #e6dede;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  min-height: 200px ;
  
}
.post hr {
  visibility: hidden;
}
.post-title {
  text-align: start;
  margin-bottom: 50px;
  
}
.post-video {
  display: flex;
  justify-content:center;
  align-items: center;
  height: 500px;
  width: auto;
}
.post-picture img{
  
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  
}
.loading-image {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}
.post-video {
  min-height: 250px;
  min-width: 250px;
}
.likes-section {
  
  position: relative;
  display:flex;
  justify-content:space-around;
  height: 25px;
  margin-top: 100px;
  margin-bottom: 10px;
  background-color: white;
  border: 0px solid #FFFFFF;
  border-radius: 40px;
  

}
.likes-section h4 {
  margin: 0px;
}
.likes {
  display:flex;
  justify-content: space-between;
  
}
.likes-comment {
  animation: colorChange 0s ease-in 1s;
  height: 20px;
  display:flex;
  align-items: flex-start;
}
.likes-comment h3 {
  margin: 0px;
}
.likes h3{
  margin: 0px;
  margin-right: 0px;
  width: 50px;
  height: 30px;
}
.nav-bar {
  display: flex;
  justify-content: center;
  height: 30px;
  align-items: center;
  
  margin: 50px 30px 0px 30px;

}
.link-bar {
  text-decoration: none;
  height: 25px;
  width: 150px;
  text-align: center;
  font-family:Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: #000000;
  font-size: 1.2rem;
  border: 1px solid rgb(225, 223, 223);
  border-radius:100px;
}
.comment {
  
  text-align: left;
  animation-name: commentAnimation;
  animation-duration: 1s;
  animation-fill-mode: both;
  opacity: 1;
  color: #000000;
  
  margin: 10px 1%;
  padding: 30px;
  border: 1px solid #000000;
  border-radius: 0px 40px 40px 40px;
}
.comment h5 {
  margin: 0px;
}
.comment-section {
  display: block;
  margin-left: 30px;
}
.comment p {
  
  opacity: 0;
  animation-name: colorChange;
  animation-duration: 200ms ;
  animation-delay: 0.9s;
  animation-fill-mode: both;
}
@media screen and (max-width: 400px) {

  
